.wrapper{max-width:600px;margin-left:auto;margin-right:auto;padding-right:24px;padding-left:24px}@media screen and (max-width: 1000px){.wrapper{padding-right:40px;padding-left:40px}}.mainvisual{max-width:1280px;height:400px;margin:0 auto}@media screen and (max-width: 1024px){.mainvisual{padding:0 10px}}.mainvisual img{width:100%;height:100%;object-fit:cover}.index{padding:35px 0 33px;margin:60px auto 0;background-color:#fff}.index ol{display:flex;flex-direction:column;gap:26px;margin-top:17px;counter-reset:count 0}.index ol li{counter-increment:count 1;position:relative;margin-left:20px;font-size:14px;font-size:0.875rem;font-family:"Noto Sans JP","ヒラギノ角ゴ ProN W3","Hiragino Kaku Gothic ProN","メイリオ",Meiryo,sans-serif;color:#333;line-height:1.2142857143}.index ol li:before{content:counter(count, decimal) ".";position:absolute;top:50%;left:-20px;transform:translateY(-50%);color:#333}.detail{padding:35px 0 33px;margin:65px auto 0}.detail__box{display:flex;gap:60px;margin-top:14px}@media screen and (max-width: 768px){.detail__box{flex-direction:column;gap:25px}}.detail__img{width:270px;height:270px}@media screen and (max-width: 768px){.detail__img{width:100%;height:auto}}.detail__img img{width:100%;height:100%;object-fit:cover}.detail__content{width:270px}@media screen and (max-width: 768px){.detail__content{width:100%}}.detail__content--title{font-size:18px;font-size:1.125rem;font-weight:bold;font-family:"Noto Sans JP","ヒラギノ角ゴ ProN W3","Hiragino Kaku Gothic ProN","メイリオ",Meiryo,sans-serif;color:#333;line-height:1.2222222222}.detail__content--info{display:flex;flex-direction:column;gap:7px;margin-top:26px;padding:19px 0;border-top:1px solid #dedede;border-bottom:1px solid #dedede}.detail__content--info .info{display:flex;justify-content:space-between}.detail__content--text{margin-top:28px}.detail__content--button{margin-top:21px}.detail__content--button .button{position:relative;font-size:14px;font-size:0.875rem;font-family:"Noto Sans JP","ヒラギノ角ゴ ProN W3","Hiragino Kaku Gothic ProN","メイリオ",Meiryo,sans-serif;color:#333;line-height:1.2142857143}.detail__content--button .button:after{content:"";position:absolute;bottom:-4px;left:0;width:100%;height:1px;border-top:1px solid #333}